.todo-list-template {
  background: white;
  width: 512px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.16), 0 6px 10px rgba(0,0,0,0.23); /* 그림자 */ 
  margin: 0 auto; /* 페이지 중앙 정렬 */
  margin-top: 6rem;
}

.title {
  padding: 1rem 2rem;
  font-size: 2.5rem;
  font-weight: bold;
  font-family: 'Signika Negative', sans-serif;
  border: 2px solid #000;
  border-bottom: 0;
}

.form-wrapper {
  padding: 1rem;
  border: 2px solid #000;
  border-top: 0;
}

.todos-wrapper {
  min-height: 5rem;
}

.todos-wrapper>div {
  border: 2px solid #000;
  border-top: 0;
  padding-bottom: 2rem;
}

